* {
  word-break: break-all;
}

body {
  font-family: 'ms_sans_serif', 'monospace';
}

body {
  background: #008083;
}

main {
  width: 80vw;
  box-sizing: border-box;
  margin: 30px auto 0 auto;
}

.window {
  width: 100%;
  height: 100%;
}

.window-title span {
  padding-left: 8px;
}

.story-link {
  display: block;
  margin-bottom: 10px;
}

.story-link span {
  margin: 0 5px;
}

.story-link-footer {
  font-size: 13px;
  display: flex;
  flex-direction: row;
}

.progress-bar {
  margin-top: 30px;
}

#scroll-view-container {
  height: 60vh;
}

.scroll-view {
  height: 100%;
  padding: 10px !important;
}

.separator {
  margin: 0 5px !important;
  height: 18px !important;
}

.credits {
  margin-top: 30px;
  font-size: 13px;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 10px;
}

.taskbar {
  width: 100%;
  position: fixed !important;
  bottom: 0;
}

.start-button {
  padding: 0 25px !important;
  margin-right: 10px;
}

.hidden {
  display: none !important;
}

.desktop {
  width: 100vw;
  height: 100vh;
  position: fixed !important;
  top: 0;
  left: 0;
  overflow: hidden;
  user-select: none;
}

.desktop-icons {
  display: flex;
  flex-direction: column;
  height: 100vh;
  gap: 20px;
  margin: 40px 0 0 30px;
}
.desktop-icon {
  max-width: 32px;
}

.desktop-icon img {
  width: 100%;
  height: auto;
}

.selection-box {
  border: 1px dashed #444;
  display: block;
  height: 0;
  width: 0;
  position: fixed;
  box-sizing: border-box;
}

@media (max-width: 768px) {
  main {
    width: 90vw;
  }
  .story-link-footer {
    display: block;
  }
  .story-link-footer span,
  .story-link-footer > a {
    display: inline-block;
  }
  .separator {
    display: none !important;
  }
  .desktop-icons {
    margin-left: 0px !important;
  }
}
